@use "../../assets//Scss/mixin.scss" as mixin;

.header {
    background-color: mixin.$primary-color;
    height: 80px;
    position: fixed;
    inset: 0;
    z-index: 50;
}

.map {
    width: 85%;
    margin: 7rem auto -2rem auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mixin.mobile {
        width: 100%;
        flex-direction: column;

    }

    iframe {
        width: 70%;
        border: none;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
        border-radius: 1px;

        @include mixin.mobile {
            width: 90%;
            margin: -3rem 0px -3rem 0px
        }

    }


    .box {
        height: 450px;
        width: 300px;
        border-radius: 1px;
        background-color: rgba(255, 255, 255, 0.87);
        color: rgb(78, 75, 75);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 3rem;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

        @include mixin.mobile {
            width: 90%;
            height: 350px;
           margin-top: 8rem;
        }

        h3 {
            margin-bottom: 2rem;
            color: mixin.$primary-color;
        }

        ul {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 3rem;

            li {
                width: 70%;
                display: flex;
                align-items: center;

                gap: 0.5rem;

                svg {
                    color: mixin.$primary-color;
                }


            }
        }


    }
}