@use "../../assets//Scss/mixin.scss" as mixin;

.homepage {

    width: auto;



    .sliderContiner {

        position: relative;



        .slide {
            width: 100%;
            height: 100vh;
            position: relative;
            color: black;


            .sliderImg {
                width: 100%;
                height: 100vh;
                object-fit: cover;

            }

            .text {
                width: 95%;
                height: 100vh;
                position: absolute;
                top: 0;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;
                z-index: 100;
                color: #fff;
                row-gap: 5rem;
                padding-left: 3rem;

                h1 {
                    font-size: 50px;

                    @include mixin.mobile {
                        font-size: 40px;

                    }
                }

                p {
                    margin-bottom: 10rem;
                }
            }

        }

        .overlay {
            position: absolute;
            height: 100vh;
            width: 100%;
            inset: 0;
            background-color: rgba(19, 18, 18, 0.596);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            color: #fff;

        }

        .down {
            position: absolute;
            bottom: 30px;
            right: 30px;
            color: #fff;
            transition: 0.7s all ease;
        }

        .hideDown {
            color: transparent;

        }


    }

    .containerWhy {
        width: 100%;
        background-color: mixin.$secondary-color;
        height: 600px;
        margin: 0 auto;
        margin-top: 15rem;
        color: mixin.$primary-color;



        @include mixin.tablet {
            position: relative;

        }



        .container {
            width: 85%;
            height: 100%;
            margin: 0 auto;
            display: flex;

            @include mixin.tablet {
                flex-direction: column;

            }

            img {
                margin-top: -3rem;
                margin-right: 5rem;
                width: 400px;
                height: 500px;
                box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

                @include mixin.tablet {

                    width: 250px;
                    height: 320px;
                    margin-top: -7rem;
                    margin-right: 0;

                }


                @include mixin.mobile {

                    display: none;

                }
            }


            .box {
                height: 100%;
                display: grid;
                padding: 1rem;

                @include mixin.tablet {
                    margin-top: 3rem;
                }


                @include mixin.mobile {
                    margin-top: 1.5rem;

                }

                .description {
                    padding-top: 1.5rem;
                    display: flex;
                    flex-direction: column;
                    row-gap: 2rem;

                    @include mixin.tablet {
                        row-gap: 1.5rem;
                    }

                    h3 {}

                    h2 {}

                    p {
                        color: #eeee;
                        font-size: 14px;
                        text-align: justify;

                    }
                }

                .icons {
                    display: flex;
                    column-gap: 2rem;
                    align-self: flex-start;
                    margin-bottom: 2rem;
                    color: white;

                    @include mixin.tablet {
                        flex-direction: column;
                        position: absolute;
                        top: 5rem;
                        right: 7rem;
                        gap: 10px;

                    }


                    @include mixin.mobile {
                        display: flex;
                        flex-direction: row;
                        bottom: -360px;
                        left: 120px;
                        align-items: center;
                        justify-content: center;
                        margin-right: 0.5rem;
                    }



                    div {
                        display: flex;
                        place-items: center;
                        background-color: mixin.$primary-color;
                        padding: 1rem;
                        border-radius: 5px;

                        @include mixin.mobile {
                            font-size: 10px;
                            width: 120px;
                            padding: 0.5rem;

                        }

                        span {
                            margin-left: 5px;
                        }
                    }


                }




            }



        }

    }

}